@font-face {
  font-family: "Open Sans";
  src: url('../fonts/OpenSans-Light.woff2') format('woff2'), url('../fonts/OpenSans-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url('../fonts/OpenSans-Regular.woff2') format('woff2'), url('../fonts/OpenSans-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url('../fonts/OpenSans-SemiBold.woff2') format('woff2'), url('../fonts/OpenSans-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url('../fonts/OpenSans-Bold.woff2') format('woff2'), url('../fonts/OpenSans-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding-left: 170px;
  padding-right: 170px;
}

body {
  font-family: "Open Sans";
}

h1,
h2 {
  text-transform: uppercase;
  font-weight: 600;
}

h1 {
  font-size: 33px;
  line-height: 45px;
}

h2 {
  font-size: 30px;
  line-height: 42px;
}

h2 span {
  color: #ff0016;
}

h3 {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
}

.button {
  display: inline-block;
  width: 310px;
  height: 66px;
  line-height: 62px;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  background-color: #111546;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s linear;
}

.button:hover,
.button:focus {
  background-color: #ff0016;
}

.button:active {
  background: #b3000f;
}

.nav a {
  margin: 0 22px;
}

.header {
  min-height: 740px;
  height: 100vh;
  max-height: 1080px;
  background: #111546 url(../img/header-bg.jpg) no-repeat center / cover;
  color: #fff;
}

.header__wrapper {
  max-width: 1440px;
  height: inherit;
  margin: 0 auto;
  padding: 0 70px;
  box-sizing: border-box;
}

.header__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 18px;
  box-sizing: border-box;
}

.header a {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  line-height: 26px;
  font-weight: 600;
}

.header__nav {
  display: flex;
}

.header__lang {
  margin-left: 22px;
}

.header__lang a {
  margin: 0 2px;
  padding: 10px;
  border-radius: 50%;
}

.header__lang--current {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid #9d9eab66;
  backdrop-filter: blur(12px);
}

.header__phone {
  text-align: right;
  font-size: 12px;
  line-height: 16px;
}

.header__phone a {
  display: block;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  margin-bottom: 4px;
}

.header__body {
  width: 100%;
  max-width: 500px;
  padding-left: 140px;
  margin-top: auto;
  display: flex;
  align-items: center;
  height: 100%;
  max-height: 900px;
}

.header__bodyWrapper {
  transform: translateY(-20%);
}

.header__text {
  margin-top: 20px;
  padding-right: 20px;
}

.header__button {
  margin-top: 50px;
  background-color: #9596a4;
  border: 3px solid #9d9eab66;
}

.about {
  padding: 100px 0;
}

.about__wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.about__item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about__item:nth-child(even) {
  flex-direction: row-reverse;
}

.about__col {
  width: 50%;
  margin-bottom: -5px;
}

.about__title {
  margin-bottom: 30px;
}

.about__text {
  margin-bottom: 30px;
  line-height: 28px;
}

.about__info {
  margin-left: 66px;
  max-width: 432px;
}

.about__info--first {
  margin-left: auto;
  margin-right: 66px;
}

.about__info--last {
  display: flex;
  align-items: center;
}

.about__info--last .about__text {
  margin-left: 33px;
  max-width: 220px;
}

.founder {
  background-color: #F2F2F2;
  padding: 90px 0;
}

.founder__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.founder__person {
  max-width: 234px;
  text-align: center;
}

.founder__img {
  margin-bottom: 22px;
}

.founder__img img {
  border-radius: 50%;
}

.founder__name {
  display: inline-block;
  margin-bottom: 6px;
}

.founder__quote {
  max-width: 480px;
  margin-left: 126px;
  font-weight: 300;
  font-size: 21px;
  line-height: 30px;
  position: relative;
}

.founder__quote::before,
.founder__quote::after {
  content: '';
  display: block;
  background: url(../img/quote.svg) left center repeat-x / contain;
  position: absolute;
}

.founder__quote::before {
  width: 124px;
  height: 114px;
  top: -46px;
  left: 0;
  transform: translate(-100%, -100%);
}

.founder__quote::after {
  width: 206px;
  height: 190px;
  bottom: -46px;
  right: 0;
  transform: translate(50%, 100%) rotate(180deg);
}

.why {
  padding: 75px 0 120px;
}

.why__title {
  text-align: right;
}

.why__text {
  display: block;
  width: 170px;
  margin: 60px auto 10px;
  transform: translateX(-50%);
}

.why__list {
  display: flex;
  justify-content: space-between;
  color: #fff;
}

.why__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 20px;
  border-radius: 50%;
  text-align: center;
  box-sizing: border-box;
}

.why__item--small {
  width: 200px;
  height: 200px;
  margin-top: 80px;
  background-color: #ff0016;
}

.why__item--small .why__digit {
  font-size: 52px;
  line-height: 75px;
  margin-bottom: 0px;
}

.why__item--middle {
  width: 300px;
  height: 300px;
  background-color: #ff0016;
}

.why__item--middle .why__digit {
  text-align: right;
}

.why__item--large {
  width: 377px;
  height: 377px;
  margin-top: 166px;
  padding: 0 40px;
  background-color: #111546;
}

.why__item--large .why__digit {
  margin-bottom: 45px;
}

.why__item--large .why__caption {
  text-align: right;
  padding-left: 50px;
}

.why__item--partners {
  color: #111546;
  max-width: 220px;
}

.why__item--partners .why__caption {
  text-align: right;
}

.why__digit {
  display: block;
  margin-bottom: 30px;
  font-weight: 600;
  font-size: 105px;
  line-height: 78px;
}

.why__caption {
  display: block;
  font-weight: 600;
}

.why__partners {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 170px;
}

.advice {
  height: 330px;
  background: #9596a4 url(../img/cs-bg.jpg) no-repeat center / cover;
  color: #fff;
}

.advice__wrapper {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.advice__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 350px;
  height: 65px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  letter-spacing: 0.4px;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid #9d9eab66;
  border-radius: 5px;
  cursor: pointer;
  backdrop-filter: blur(4px);
  position: relative;
  transition: background 0.3s linear;
}

.advice__button:hover,
.advice__button:focus {
  background-color: #ff0016;
  border-color: transparent;
}

.advice__button:active {
  background: #b3000f;
}

.advice__button::after {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  margin-left: 20px;
  background: url(../img/cs-button.svg) no-repeat / contain;
}

.advice__caption {
  margin-top: 20px;
}

.vakancy {
  padding: 90px 0;
  text-align: center;
}

.vakancy h2 {
  padding-left: 50px;
  text-align: left;
}

.vakancy__list {
  margin-top: 92px;
  text-align: left;
}

.vakancy__item {
  padding: 62px 50px 32px;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  border-collapse: collapse;
}

.vakancy__item--special {
  position: relative;
}

.vakancy__item--special::before {
  content: 'Специальные условия';
  display: block;
  padding: 2px 26px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 16px;
  background-color: #ff0016;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 25px;
  left: 50px;
}

.vakancy__item--special .vakancy__button::after {
  content: '';
  display: inline-block;
  vertical-align: sub;
  width: 20px;
  height: 20px;
  margin-left: 10px;
  background: url(../img/special.svg) no-repeat center / contain;
}

.vakancy__head {
  display: flex;
  justify-content: space-between;
}

.vakancy__info {
  display: flex;
}

.vakancy__img {
  width: 82px;
  height: 82px;
}

.vakancy__img img {
  width: inherit;
  height: inherit;
  object-fit: cover;
}

.vakancy__name {
  margin-left: 22px;
  max-width: 500px;
}

.vakancy__location {
  font-size: 14px;
}

.vakancy__price {
  flex-basis: 220px;
  font-size: 14px;
  line-height: 26px;
  text-align: right;
}

.vakancy__price span {
  display: block;
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
}

.vakancy__body {
  margin-top: 46px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 22px;
}

.vakancy__conditions li {
  list-style: disc inside;
}

.vakancy__shift:last-child::before {
  margin-left: 7px;
}

.vakancy__shift::before {
  content: '';
  display: inline-block;
  width: 11px;
  height: 11px;
  margin-right: 5px;
  background: url(../img/job-mark.svg) no-repeat / contain;
}

.vakancy__button {
  width: 170px;
  height: 45px;
  line-height: 43px;
  font-size: 14px;
}

.vakancy__more {
  margin: 80px auto 0;
  width: 250px;
  height: 65px;
  line-height: 63px;
  text-align: center;
}

.start h2 {
  text-align: center;
}

.start__list {
  display: flex;
  justify-content: space-between;
  margin-top: 120px;
}

.start__item {
  list-style: none;
  width: 310px;
  height: 310px;
  background: center no-repeat / cover;
  position: relative;
}

.start__item--1 {
  background-image: url(../img/step-1.jpg);
}

.start__item--2 {
  background-image: url(../img/step-2.jpg);
}

.start__item--3 {
  background-image: url(../img/step-3.jpg);
}

.start__title {
  display: inline-block;
  width: 100%;
  height: 100px;
  line-height: 100px;
  padding: 25px 0px 25px 60px;
  font-size: 20px;
  line-height: 24px;
  background-color: #f6f6f6;
  position: absolute;
  bottom: 0px;
  box-sizing: border-box;
}

.start__title::before {
  content: '';
  display: inline-block;
  width: 5px;
  height: 50px;
  background-color: #ff0016;
  position: absolute;
  left: 30px;
}

.quick {
  padding-top: 220px;
  padding-bottom: 120px;
}

.quick__title {
  max-width: 740px;
  font-size: 52px;
  line-height: 75px;
  position: relative;
  padding-left: 100px;
}

.quick__title::after {
  content: '';
  display: inline-block;
  width: 168px;
  height: 168px;
  background: url(../img/registration.jpg) center no-repeat / cover;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%, -50%);
}

.quick__text {
  display: flex;
  margin-top: 56px;
  font-size: 20px;
  line-height: 32px;
  padding-left: 100px;
}

.quick__text p {
  width: 348px;
  margin-right: 48px;
}

.quick__text span {
  color: #ff0016;
  font-weight: 600;
}

@media screen and (max-width: 1024px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media screen and (max-width: 480px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}